import { Helmet } from 'react-helmet-async'
import styles from './Accueil.module.css'


export default function Accueil() {
    return <>
         <Helmet>
           <meta name="description" content="Protfolio KOUADIO" />
           <title>Accueil - Portfolio de KOUADIO</title>
         </Helmet>


        <section className={styles.presentation}>
            <img src="/image/joel.jpg" alt="KOUADIO Konan Henri Jol" />
            <div>
                <h2>Bonjour, je suis KOUADIO Konan Henri Jol</h2>
                <p>
                 Diplm dune matrise en gestion des RH, je me suis redirig vers ma passion qui est linformatique et plus spcialement la programmation. Jaime relever les dfis lis  la polyvalence de linformatique et dvelopper des applications et sites utiles, variables et ergonomiques.
                </p>
                <p>
                Dveloppeur depuis deux ans, je souhaite dsormais mettre mes comptences et mon exprience au service dune entreprise. Ayant travaill sur plusieurs diffrents projets (dveloppement apps mobiles, cration de site web pour lentreprise FDFP.), je peux bien mintgrer dans une quipe de dveloppement grce  mes connaissances (HTML5, Java/J2EE, C#)
                </p>
            </div>
        </section>
        
        <section className={styles.presentation + ' ' +  styles.inverse}>
            <img src="/image/lang.jpg" alt="KOUADIO Konan Henri Jol" />
            <div>
                <h2>Langages </h2>
                <ul>
                    <li>Java</li>
                    <li>C#</li>
                    <li>HTML5</li>
                    <li>CSS</li>
                    <li>Java Script</li>
                </ul>   
                <br />
                <br />   
                <h2>Frameworks</h2>
                <ul>
                    <li>Node.js</li> 
                    <li>Express.js</li>
                    <li>React</li> 
                    <li>Boostrap</li> 

                </ul>
                <br />
                <br />
                <h2>Autres</h2>
                <ul>
                    <li>MySql</li>
                    <li>Sql lite</li>
                    <li>Mongo Db</li>
                </ul>
                       
            </div>
        </section>

    </>
}